import React, { useEffect, useState } from "react";
import { jwtDecode } from "jwt-decode"; // jwt-decode  import
import "./Profile.css"; //  
import NavBarModule from "../components/NavBar/NavBar";
import { useParams, useNavigate, Link } from "react-router-dom";
import { googleLogout } from "@react-oauth/google";

const currentUserGoogleId =
  localStorage.getItem("token") && jwtDecode(localStorage.getItem("token")).sub;

function ProfilePage() {
  const { googleId } = useParams();
  const navigate = useNavigate();
  const [userInfo, setUserInfo] = useState([]);
  const [userRecipes, setUserRecipes] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);
  const [isEditing, setIsEditing] = useState(false); //   
  const [formData, setFormData] = useState({
    name: "",
    picture: null, // Base64  
  });


  useEffect(() => {
    const token = localStorage.getItem("token");

    if (!token) {
      setError("  .  .");
      setLoading(false);
      return;
    }

    try {
      const decoded = jwtDecode(token);
      setUserInfo(decoded);

      const fetchUserInfo = async () => {
        try {
          const response = await fetch(
            `http://localhost:5000/profile/${googleId}`
          );

          if (!response.ok) {
            throw new Error("Failed to fetch user info");
          }

          const data = await response.json();
          setUserInfo(data.user);

          //   
          setFormData({
            name: data.user.name || "",
            picture: data.user.picture || null,
          });
          setLoading(false);
        } catch (error) {
          console.error("Error fetching user info:", error.message);
          setError(error.message);
        }
      };

      //    
      const fetchUserRecipes = async () => {
        try {
          const response = await fetch(
            `http://localhost:5000/recipes/user/${googleId}`
          );

          if (!response.ok) {
            throw new Error("Failed to fetch user recipes");
          }

          const data = await response.json();
          setUserRecipes(data);
        } catch (error) {
          console.error("Error fetching user recipes:", error.message);
          setError(error.message);
        } finally {
          setLoading(false);
        }
      };

      fetchUserInfo();
      fetchUserRecipes();
    } catch (err) {
      console.error("Failed to decode token:", err.message);
      setError("  .  .");
      setLoading(false);
    }
  }, [googleId]);

  //    ( )
  const handleFileChange = (e) => {
    const file = e.target.files[0];
    if (file) {
      const reader = new FileReader();
      reader.onloadend = () => {
        setFormData((prev) => ({
          ...prev,
          picture: reader.result,
        }));
      };
      reader.readAsDataURL(file);
    }
  };

  //   
  const handleInputChange = (e) => {
    const { name, value } = e.target;
    setFormData((prev) => ({
      ...prev,
      [name]: value,
    }));
  };

  //  
  const handleSaveChanges = async () => {
    if (!window.confirm(" ?")) return;

    try {
      const token = localStorage.getItem("accessToken");
      if (!token) {
        alert(" .");
        return;
      }

      const payload = {
        name: formData.name,
        picture: formData.picture, // Base64  
      };

      console.log("FormData being sent:", payload);

      const response = await fetch(`http://localhost:5000/profile/${googleId}`, {
        method: "PUT",
        headers: {
          "Content-Type": "application/json",
          Authorization: `Bearer ${token}`,
        },
        body: JSON.stringify(payload),
      });

      if (response.ok) {
        const updatedUser = await response.json();
        setUserInfo(updatedUser.user);
        setIsEditing(false);
        alert("  .");
        navigate(`/profile/${googleId}`);
      } else {
        const errorData = await response.json();
        console.error("Failed to update profile:", errorData);
        alert("  .");
      }
    } catch (err) {
      console.error("Error updating profile:", err);
      alert("    .");
    }
  };

  //  
  const handleDeleteProfile = async () => {
    if (!window.confirm("  ?")) return;

    const handleLogout = () => {
      //    
      localStorage.clear();
      console.log("Access token has been removed.");
  
      // Google OAuth  
      googleLogout();
  
      //   
      navigate("/budda");
    };

    try {
      const token = localStorage.getItem("accessToken");
      const response = await fetch(`http://localhost:5000/profile/${googleId}`, {
        method: "DELETE",
        headers: {
          Authorization: `Bearer ${token}`,
        },
      });

      if (response.ok) {
        alert("  .");
        handleLogout();
      } else {
        const errorData = await response.json();
        console.error("Failed to delete profile:", errorData);
        alert("  .");
      }
    } catch (err) {
      console.error("Error deleting profile:", err);
      alert("    .");
    }
  };

  if (loading) return <p>Loading...</p>;
  if (error) return <p>Error: {error}</p>;

  return (
    <div>
      <NavBarModule />
      <div className="wrapp-profile-container">
        <div className="profile-container">
          <h1> </h1>
          <div className="profile-picture">
            {isEditing ? (
              <label htmlFor="file-input">
                <img
                  className="picture"
                  src={
                    formData.picture ||
                    "https://via.placeholder.com/150?text=No+Image"
                  }
                  alt=" "
                />
                <input
                  type="file"
                  id="file-input"
                  style={{ display: "none" }}
                  accept="image/*"
                  onChange={handleFileChange}
                />
              </label>
            ) : (
              <img
                className="picture"
                src={userInfo.picture || "https://via.placeholder.com/150?text=No+Image"}
                alt=" "
              />
            )}
          </div>
          <div className="profile-details">
            {isEditing ? (
              <input
                type="text"
                name="name"
                value={formData.name}
                onChange={handleInputChange}
                placeholder=" "
              />
            ) : (
              <p>
                <strong>Name:</strong> {userInfo.name}
              </p>
            )}
            <p>
              <strong>E-mail:</strong> {userInfo.email}
            </p>
            <p>
              <strong>Google ID:</strong> {userInfo.googleId}
            </p>
          </div>
          {currentUserGoogleId === googleId && (
            <div>
            {isEditing ? (
              <>
                <button onClick={handleSaveChanges}>Save</button>
                <button onClick={() => setIsEditing(false)}>Cancel</button>
              </>
            ) : (
              <button onClick={() => setIsEditing(true)}>Edit Profile</button>
            )}
            <button onClick={handleDeleteProfile}>Delete Account</button>
          </div>
          )}
        </div>
      </div>
      <div className="user-recipes-container">
        <h2 className="user-recipes-title">{userInfo.name}  </h2>
        {userRecipes.length > 0 ? (
          <div className="recipes-grid">
            {userRecipes.map((recipe) => (
              <Link
                to={`/recipes/${recipe._id}`}
                key={recipe._id}
                className="recipe-card"
              >
                <div className="wrapp_recipe">
                  <img
                    src={
                      recipe.image ||
                      "https://via.placeholder.com/150?text=No+Image"
                    }
                    alt={recipe.recipeName}
                    className="recipe-image"
                  />
                  <p className="recipe-name">{recipe.recipeName}</p>
                </div>
              </Link>
            ))}
          </div>
        ) : (
          <p style={{ textAlign: "center" }}>
            This chef haven't uploaded any recipes yet.
          </p>
        )}
      </div>
    </div>
  );
}

export default ProfilePage;
