import { useState } from "react";
import "./SearchPage.css";
import NavBar from "../components/NavBar/NavBar";
import Footer from "../components/Footer/Footer";
import { Link } from "react-router-dom";

function SearchPage() {
  //   
  const [filters, setFilters] = useState({
    categories: "",
    situation: "",
    ingredient: "",
    count: "",
    time: "",
    difficulty: "",
  });

  //    
  const [results, setResults] = useState([]);

  //   
  const handleFilterChange = (e) => {
    const { name, value } = e.target;
    setFilters((prev) => ({
      ...prev,
      [name]: value,
    }));
  };

  //    
  const handleSearch = async () => {
    const filtersToSend = {
      ...filters,
      count: filters.count ? parseInt(filters.count, 10) : undefined, //  
    };

    console.log("Filters being sent:", filtersToSend);

    try {
      //    
      const response = await fetch("http://localhost:5000/search", {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify(filtersToSend),
      });

      if (!response.ok) {
        throw new Error(`HTTP error! status: ${response.status}`);
      }

      const data = await response.json();
      setResults(data); //   
    } catch (error) {
      console.log("Error fetching search results", error);
    }
  };

  const handleRefresh = () => {
    //  
    setFilters({
      categories: "",
      situation: "",
      ingredient: "",
      count: "",
      time: "",
      difficulty: "",
    });

    //   
    setResults([]);
  };

  return (
    <div className="SearchPage">
      <NavBar />
      <div className="WrapSearchPage">
        <div className="WrapOptions">
          <div>
            <label className="optionsLabel">category</label>
            <select
              className="optionsSelect"
              name="categories"
              value={filters.categories}
              onChange={handleFilterChange}
            >
              <option value=""></option>
              <option value="main"></option>
              <option value="soup">/</option>
              <option value="dessert"></option>
            </select>
          </div>
          <div>
            <label className="optionsLabel">situation</label>
            <select
              className="optionsSelect"
              name="situation"
              value={filters.situation}
              onChange={handleFilterChange}
            >
              <option value=""></option>
              <option value="daily"></option>
              <option value="diet"></option>
            </select>
          </div>
          <div>
            <label className="optionsLabel">ingredient</label>
            <select
              className="optionsSelect"
              name="ingredient"
              value={filters.ingredient}
              onChange={handleFilterChange}
            >
              <option value=""></option>
              <option value="meat"></option>
              <option value="vegetable"></option>
            </select>
          </div>
          <div>
            <label className="optionsLabel">count</label>
            <select
              className="optionsSelect"
              name="count"
              value={filters.count}
              onChange={handleFilterChange}
            >
              <option value=""></option>
              <option value="1">1</option>
              <option value="2">2</option>
            </select>
          </div>
          <div>
            <label className="optionsLabel">time</label>
            <select
              className="optionsSelect"
              name="time"
              value={filters.time}
              onChange={handleFilterChange}
            >
              <option value=""></option>
              <option value="10min">10</option>
              <option value="20min">20</option>
            </select>
          </div>
          <div>
            <label className="optionsLabel">difficulty</label>
            <select
              className="optionsSelect"
              name="difficulty"
              value={filters.difficulty}
              onChange={handleFilterChange}
            >
              <option value=""></option>
              <option value="easy"></option>
              <option value="hard"></option>
            </select>
          </div>
          <button className="searchBtn" onClick={handleSearch}>
            Search
          </button>
          <button className="searchBtn" onClick={handleRefresh}>
            Refresh
          </button>
        </div>
        <div className="SearchResults">
          {results.length > 0 ? (
            results.map((result, index) => (
              <Link to={`/recipes/${result._id}`} className="resultCardLink">
                <div key={index} className="resultCard">
                  <h3>{result.recipeName}</h3>
                  <div className="wrapp_info">
                    <div className="wrapp_IMG">
                      {result.image ? (
                        <img
                          src={result.image}
                          alt={result.recipeName}
                          className="result_IMG"
                        />
                      ) : (
                        <div className="IMG">IMG</div>
                      )}
                    </div>
                  </div>
                </div>
              </Link>
            ))
          ) : (
            <div className="wrappNoResults">
              <p className="noResults">No results found :(</p>
            </div>
          )}
        </div>
      </div>
      <Footer />
    </div>
  );
}

export default SearchPage;
