import "../components/IntoroduceRecipe/IntroduceRecipe.css"; //   
import React, { useEffect } from "react";
import ReactQuill from "react-quill";
import useRecipeForm from "../hooks/useRecipeForm"; //   
import { useParams, useNavigate } from "react-router-dom";

function RecipeEdit() {
  const {
    formData,
    setFormData,
    handleFileChange,
    handleInputChange,
    addIngredient,
    removeIngredient,
    handleIngredientChange,
    handleStepsChange,
  } = useRecipeForm();
  const { id } = useParams(); //  ID 
  const navigate = useNavigate();

  //      
  useEffect(() => {
    const fetchRecipe = async () => {
      try {
        const response = await fetch(`http://localhost:5000/recipes/${id}`);
        if (!response.ok) {
          throw new Error(`Failed to fetch recipe: ${response.status}`);
        }
        const recipeData = await response.json();

        //   
        setFormData({
          recipeName: recipeData.recipeName || "",
          recipeIntroduction: recipeData.recipeIntroduction || "",
          categories: recipeData.categories || {
            type: "",
            situation: "",
            ingredient: "",
          },
          info: recipeData.info || { count: "", time: "", difficulty: "" },
          ingredients: recipeData.ingredients || [],
          steps: recipeData.steps || "",
          image: recipeData.image || null, // Base64 
        });
      } catch (error) {
        console.error("Error fetching recipe:", error);
      }
    };
    fetchRecipe();
  }, [id, setFormData]);

  //   
  const handleUpdateSubmit = async () => {
    if (window.confirm(" ?")) {
      try {
        const response = await fetch(`http://localhost:5000/recipes/${id}`, {
          method: "PUT",
          headers: {
            "Content-Type": "application/json",
          },
          body: JSON.stringify(formData),
        });

        if (response.ok) {
          alert(" !");
          navigate(`/recipes/${id}`); //     
        } else {
          alert(" .");
        }
      } catch (error) {
        console.error(" :", error);
      }
    }
  };

  return (
    <div>
      <div className="recipeInfo">
        <div className="recipeInfo_left">
          <div className="name">
            <p className="nameP">Name of recipe</p>
            <input
              type="text"
              name="recipeName"
              className="recipeName"
              size={80}
              value={formData.recipeName}
              onChange={handleInputChange}
            />
          </div>
          <div className="name">
            <p className="nameP">Introduce recipe</p>
            <input
              type="text"
              name="recipeIntroduction"
              className="recipeIntroduction"
              size={80}
              value={formData.recipeIntroduction}
              onChange={handleInputChange}
            />
          </div>
          <div className="name">
            <p className="nameP">Categories</p>
            <form className="form">
              <select
                name="type"
                value={formData.categories.type}
                onChange={handleInputChange}
                className="select"
              >
                <option value=""></option>
                <option value="main"></option>
                <option value="soup">/</option>
                <option value="dessert"></option>
              </select>
              <select
                name="situation"
                value={formData.categories.situation}
                onChange={handleInputChange}
                className="select"
              >
                <option value=""></option>
                <option value="daily"></option>
                <option value="diet"></option>
              </select>
              <select
                name="ingredient"
                value={formData.categories.ingredient}
                onChange={handleInputChange}
                className="select"
              >
                <option value=""></option>
                <option value="meat"></option>
                <option value="vegetable"></option>
              </select>
            </form>
          </div>
          <div className="name">
            <p className="nameP">Information</p>
            <form className="form">
              <p></p>
              <select
                name="count"
                value={formData.info.count}
                onChange={handleInputChange}
                className="select"
              >
                <option value=""></option>
                <option value="1">1</option>
                <option value="2">2</option>
              </select>
              <p></p>
              <select
                name="time"
                value={formData.info.time}
                onChange={handleInputChange}
                className="select"
              >
                <option value=""></option>
                <option value="10min">10</option>
                <option value="20min">20</option>
              </select>
              <p></p>
              <select
                name="difficulty"
                value={formData.info.difficulty}
                onChange={handleInputChange}
                className="select"
              >
                <option value=""></option>
                <option value="easy"></option>
                <option value="hard"></option>
              </select>
            </form>
          </div>
        </div>
        <div className="recipeInfo_right">
          <label htmlFor="file-input">
            <img
              className="recipeInfo_right_img"
              src={
                formData.image ||
                "https://recipe1.ezmember.co.kr/img/pic_none4.gif"
              }
              alt=" "
            />
          </label>
          <input
            type="file"
            id="file-input"
            style={{ display: "none" }}
            accept="image/*"
            onChange={handleFileChange}
          />
        </div>
      </div>
      <div className="IngredientInfo">
        <div className="Ingredient">
          <p className="p">Ingredients</p>
          <button onClick={addIngredient} className="addDiv">
            Add Ingredients
          </button>
        </div>
        <div className="inputDiv-container">
          {formData.ingredients.map((ingredient, index) => (
            <div key={index} className="inputDiv">
              <input
                type="text"
                placeholder="Name"
                value={ingredient.name}
                onChange={(e) =>
                  handleIngredientChange(index, "name", e.target.value)
                }
              />
              <input
                type="text"
                placeholder="Quantity"
                value={ingredient.quantity}
                onChange={(e) =>
                  handleIngredientChange(index, "quantity", e.target.value)
                }
              />
              <input
                type="text"
                placeholder="Unit"
                value={ingredient.unit}
                onChange={(e) =>
                  handleIngredientChange(index, "unit", e.target.value)
                }
              />
              <input
                type="text"
                placeholder="Note"
                value={ingredient.note}
                onChange={(e) =>
                  handleIngredientChange(index, "note", e.target.value)
                }
              />
              <button onClick={() => removeIngredient(index)}>Delete</button>
            </div>
          ))}
        </div>
      </div>
      <div className="howToCook">
        <ReactQuill
          value={formData.steps}
          onChange={handleStepsChange}
          style={{ width: "", height: "600px" }}
        />
      </div>
      <div className="wrappSubmitBtn">
        <button onClick={handleUpdateSubmit} className="submitBtn">
          Update Recipe
        </button>
      </div>
    </div>
  );
}

export default RecipeEdit;
