import "./Community.css";
import Navbar from "../../components/NavBar/NavBar";
import Footer from "../../components/Footer/Footer";
import React, { useEffect, useState, useRef } from "react";
import { io } from "socket.io-client";
import { jwtDecode } from "jwt-decode";

const socket = io("http://localhost:5000"); //  Socket.IO  

function Community() {
  const [messages, setMessages] = useState([]);
  const [input, setInput] = useState("");
  const [userInfo, setUserInfo] = useState(null); //   
  const chatBoxRef = useRef(null); // chat-box   Ref 

  useEffect(() => {
    // JWT    
    const token = localStorage.getItem("accessToken");
    if (token) {
      try {
        const decoded = jwtDecode(token);
        setUserInfo({
          googleId: decoded.googleId,
          name: decoded.name,
          picture: decoded.picture,
        });
      } catch (error) {
        console.error("Invalid token", error);
      }
    }

    //  
    socket.on("message", (msg) => {
      setMessages((prev) => [...prev, msg]);
    });

    //      
    return () => {
      socket.disconnect();
    };
  }, []);

  useEffect(() => {
    //      
    if (chatBoxRef.current) {
      chatBoxRef.current.scrollTop = chatBoxRef.current.scrollHeight;
    }
  }, [messages]);

  const sendMessage = () => {
    if (input.trim() && userInfo) {
      const messageData = {
        text: input,
        senderId: userInfo.googleId,
        senderName: userInfo.name,
        userImg: userInfo.picture,
      };
      socket.emit("message", messageData); //   
      setMessages((prev) => [
        ...prev,
        { ...messageData, self: true }, //    
      ]);
      setInput("");
    }
  };

  const handleKeyDown = (event) => {
    if (event.key === "Enter") {
      sendMessage();
    }
  };

  return (
    <div>
      <Navbar />
      <div className="wrapp">
        <h1>BUDDA - Community</h1>
        <div className="chat-box" ref={chatBoxRef}>
          {" "}
          {/* chat-box ref  */}
          {messages.map((msg, index) => (
            <p
              className="chat_content"
              key={index}
              style={{
                textAlign: msg.self ? "right" : "left",
                color: msg.self ? "black" : "black",
              }}
            >
              {!msg.self && ( //       
                <img
                  src={userInfo.picture}
                  alt={`${userInfo.name}'s profile`}
                  className="userProfileImage_community"
                />
              )}
              {msg.self ? `${userInfo.name} : ${msg.text}` : ` ${msg.text}`}
            </p>
          ))}
        </div>
        <input
          type="text"
          value={input}
          onChange={(e) => setInput(e.target.value)}
          onKeyDown={handleKeyDown} //   
          placeholder=" "
        />
        <button onClick={sendMessage}></button>
      </div>
      <Footer />
    </div>
  );
}

export default Community;
